    p.mb-3 Legislacin laboral es el conjunto de principios, acciones y normas que regulan directa e indirectamente las relaciones entre empleadores y trabajadores, y de estos con el Estado, con el objeto de garantizar los derechos fundamentales de los trabajadores y la proteccin del trabajo. Todo lo anterior tiene el nico fin de lograr la paz social, siendo esta la finalidad del cdigo laboral colombiano. (Ginnamarce, 2014)
    p.mb-3 En este componente se abordar la normatividad requerida para el reconocimiento de los beneficios a los empleados en Colombia.

    p.mb-3 Para apropiarse del conocimiento de la normatividad vigente, en cuanto a la legislacin laboral en Colombia, la cual debe ser aplicada en cualquier contexto laboral de las empresas, es necesario realizar una revisin del Cdigo Sustantivo de Trabajo (CST) y los cambios reflejados en el Decreto 1072 de 2015. 

