    p.mb-3 Es un conjunto de principios, reglas y procedimientos especficos que son adoptados por una entidad para preparar y realizar los estados o documentos contables.  (Moncayo, 2017)
    p Consultando polticas contables de entidades, se encuentra la siguiente estructura, la cual es definida por las Normas Internacionales de la Informacin Financiera (NIIF):

