      .h2 1.2 Contrato de trabajo, elementos y tipos de contrato de trabajo
    h3.mb-3.titulo-tercero Definicin contrato de trabajo

    p.mb-3 De acuerdo con el CST en el Artculo 22

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8
        .bloque-texto-d.color-secundario.p-4
          .bloque-texto-d__texto.mb-1
            i.fas.fa-quote-left
            span.h5 Contrato de trabajo es aquel por el cual una persona natural se obliga a prestar un servicio personal a otra persona natural o jurdica, bajo la continuada dependencia o subordinacin de la segunda y mediante la remuneracin o salario
            i.fas.fa-quote-right
          .bloque-texto-d__autor 
            span  (Secretara del Senado, 2020, art. 22)

    p.mb-5 Es importante resaltar que se debe profundizar el tema relacionado en el CST.

    h5.mb-3 Elementos del contrato de trabajo

    p.mb-3 Todo contrato de trabajo deber contener una serie de elementos que lo configuran legalmente; elementos que son esenciales en la concertacin o convenio que pactan el empleador y trabajador, configurando la naturaleza de la relacin laboral entre las partes.

    p.mb-3 El Artculo 23 del CST seala los tres elementos que contiene un contrato de trabajo:

    LineaTiempoD.color-secundario.my-5
      .row(numero="1" titulo="La prestacin personal del servicio")
        .col-lg-6
          figure
            img(src='@/assets/curso/images/tema1/img_2.svg', alt="", style="width:400px; display:block; margin:0 auto;")
          
        .col-lg-6.p-4
          p.mb-0 El primer elemento de un contrato de trabajo es la prestacin personal del servicio por parte del trabajador. <b>La actividad personal del trabajador, es decir, realizada por s mismo</b>, segn el CST.
          p En otras palabras, se podra decir que el trabajador debe realizar el trabajo contratado de forma personalizada, de modo que la ejecucin no puede ser delegada a un tercero. 

      .row(numero="2" titulo="La continuada subordinacin")
        .col-lg-6
          figure
            img(src='@/assets/curso/images/tema1/img_3.svg', alt="", style="width:400px; display:block; margin:0 auto;")
          
        .col-lg-6.p-4
          p.mb-3 Es el segundo elemento de un contrato de trabajo, que se podra definir como el deber de un trabajador de obedecer las instrucciones y rdenes que el empleador le formule y donde el trabajador no tiene autonoma ni libertad respecto al cmo y cundo debe hacer el trabajo para el cual fue contratado. 

          p.mb-3 El CST la define as:

          p.mb-3  La continuada subordinacin o dependencia del trabajador respecto del empleador que faculta a este para exigirle el cumplimiento de rdenes, en cualquier momento, en cuanto al modo, tiempo, cantidad de trabajo, e imponerle reglamentos, la cual debe mantenerse por todo el tiempo de duracin del contrato. Todo ello sin que afecte el honor, la dignidad y los derechos mnimos del trabajador en concordancia con los tratados o convenios internacionales que sobre derechos humanos relativos a la materia obliguen al pas. (INCP, s.f.)

      .row(numero="3" titulo="La retribucin o remuneracin del servicio")
        .col-md-6
          figure
            img(src='@/assets/curso/images/tema1/img_4.svg', alt="", style="width:400px; display:block; margin:0 auto;")
      
        .col-lg-6.p-4
          p.mb-3 Por ltimo, se puede afirmar que cuando existe un contrato bilateral y oneroso, se confirma el derecho a una remuneracin o pago por realizar una labor.
          p.mb-3 En cuanto a lo que afirma la norma, esta lo define como: Un salario como retribucin del servicio. (INCP, s.f.)
          p.mb-4 Finalmente, para profundizar en los temas propuestos en el CST, se sugiere ingresar a travs del siguiente enlace:
        
          a.anexo(href="http://www.secretariasenado.gov.co/senado/basedoc/codigo_sustantivo_trabajo.html" target="_blank")
            .anexo__icono
              img(src="@/assets/template/icono-link.svg")
            .anexo__texto
              p Enlace web. Secretara del Senado. 
      
    p.mb-5 Cuando se consagren los tres elementos anteriormente comentados, se configura la existencia de un contrato de trabajo ante la ley, independientemente del nombre que se utilice en la relacin contractual. 

