    #t_1_3.titulo-segundo
      .h2 1.3  Deberes y derechos de empleado y empleador
    
    p.mb-3 En la relacin contractual, a diario se presentan diversas situaciones de inconformidad que afectan a las partes interesadas en la ejecucin de un contrato de trabajo; es por ello que se hace importante resaltar que la ley protege a cada una de las personas que intervienen en un conflicto laboral. Por tal motivo se invita a profundizar sobre los deberes y derechos, tanto para el empleador como para el empleado.

    .row.justify-content-center.align-items-center.mb-5
      .col-12.col-md-8.col-lg-6.col-xl-6
        p.mb-3 Los deberes y derechos estn consagrados en el CST desde el <b>Artculo 56. Obligaciones de las partes en general.</b>

        p.mb-3 De modo general, incumben al empleador obligaciones de proteccin y de seguridad para con los trabajadores, y a estos, obligaciones de obediencia y fidelidad para con el empleador.

        p.mb-3 De acuerdo con el CST, se establecen en el <b>Artculo 57. Las obligaciones especiales del empleador,</b> a saber: 
        
      .col-12.col-md-8.col-lg-6.col-xl-6
        img.img-a.my-3(src="@/assets/curso/images/tema1/img_5.svg" alt="", style="width:400px; display:block; margin:0 auto;") 
    PasosA.color-acento-contenido.mb-5(tipo="n")
      div
        h3
        p Poner a disposicin de los trabajadores, salvo estipulacin en contrario, los instrumentos adecuados y las materias primas necesarias para la realizacin de las labores.

      div
        h3 
        p Procurar a los trabajadores locales apropiados y elementos adecuados de proteccin contra los accidentes y enfermedades profesionales, de manera que se garanticen razonablemente la seguridad y la salud.

      div
        h3 
        p Prestar inmediatamente los primeros auxilios en caso de accidente o de enfermedad. A este efecto, en todo establecimiento, taller o fbrica que ocupe habitualmente ms de diez (10) trabajadores se deber mantener lo necesario segn reglamentacin de las autoridades sanitarias.

      div
        h3 
        p Pagar la remuneracin pactada en las  condiciones, perodos y lugares convenidos.

      div
        h3 
        p Guardar absoluto respeto a la dignidad personal del trabajador, a sus creencias y sentimientos.

      div
        h3 
        p Conceder al trabajador las licencias necesarias para el ejercicio del sufragio; para el desempeo de cargos oficiales transitorios de forzosa aceptacin; en caso de grave calamidad domstica debidamente comprobada; para desempear comisiones sindicales inherentes a la organizacin o para asistir al entierro de sus compaeros, siempre que avise con la debida oportunidad al empleador o a su representante y que, en los dos  (2) ltimos casos, el nmero de los que se ausenten no sea tal que perjudique el funcionamiento de la empresa. En el reglamento de trabajo se sealarn las condiciones para las licencias antedichas. Salvo convencin en contrario, el tiempo empleado en estas licencias puede descontarse al trabajador o compensarse con tiempo igual de trabajo efectivo en horas distintas de su jornada ordinaria, a opcin del empleador. 

      div
        h3 
        p Dar al trabajador que lo solicite, a la expiracin de contrato, una certificacin en que consten el tiempo de servicio, la ndole de la labor y el salario devengado; e igualmente, si el trabajador lo solicita, hacerle practicar examen sanitario y darle certificacin sobre el particular, si al ingreso o durante la permanencia en el trabajo hubiere sido sometido a examen mdico. Se considera que el trabajador, por su culpa, elude, dificulta o dilata el examen, cuando transcurrido cinco (5) das a partir de su retiro no se presenta donde el mdico respectivo para la prctica del examen, a pesar de haber recibido la orden correspondiente. 

      div
        h3 
        p Pagar al trabajador los gastos razonables de venida y de regreso, si para prestar sus servicios lo hizo cambiar de residencia, salvo si la terminacin del contrato se origina por culpa o voluntad del trabajador. Si el trabajador prefiere radicarse en otro lugar, el empleador le debe costear su traslado hasta la concurrencia de los gastos que demandara su regreso al lugar donde resida anteriormente. En los gastos de traslado del trabajador, se entienden comprendidos los de los familiares que con l convivieren.

      div
        h3 
        p Cumplir el reglamento y mantener el orden, la moralidad y el respeto a las leyes.

      div
        h3 
        p Conceder al trabajador en caso de fallecimiento de su cnyuge, compaero o compaera permanente o de un familiar hasta el grado segundo de consanguinidad, primero de afinidad y primero civil, una licencia remunerada por luto de cinco (5) das hbiles, cualquiera sea su modalidad de contratacin o de vinculacin laboral. La grave calamidad domstica no incluye la licencia por luto que trata este numeral. Este hecho deber demostrarse mediante documento expedido por la autoridad competente, dentro de los treinta (30) das siguientes a su ocurrencia.

      div
        h3 
        p Conceder en forma oportuna a la trabajadora en estado de embarazo, la licencia remunerada consagrada en el numeral 1 del Artculo 236, de forma tal que empiece a disfrutarla de manera obligatoria una (1) semana antes o dos (2) semanas antes de la fecha probable del parto, segn decisin de la futura madre conforme al certificado mdico a que se refiere el numeral 3 del citado Artculo 236. (CST, 2020, art. 57 prr. 1-11)

    p.mb-5 As mismo, se establece en el CST el <b>Artculo 58. Obligaciones especiales del trabajador</b>, en el que se relaciona:


    .col-sm.mb-5.mb-sm-0.mb-5
