    p.mb-5 Un contrato de trabajo se puede dar por terminado cuando se presente cualquiera de las siguientes causas. Segn el Artculo 61 del CST, estas son:

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-6
        ol.lista-ol--cuadro.lista-ol--separador
