    p.mb-5 El CST consagra las causas justas para dar por terminado unilateralmente el contrato de trabajo, por parte del empleador:

    PasosA.color-secundario.mb-5(tipo="n")
      div
        h3
        p El haber sufrido engao por parte del trabajador, mediante la presentacin de certificados falsos para su admisin o tendientes a obtener un provecho indebido.

      div
        h3 
        p Todo acto de violencia, injuria, malos tratamientos o grave indisciplina en que incurra el trabajador en sus labores, contra el empleador, los miembros de su familia, el personal directivo o los compaeros de trabajo.

      div
        h3 
        p Todo acto grave de violencia, injuria o malos tratamientos en que incurra el trabajador fuera del servicio, en contra del empleador, de los miembros de su familia o de sus representantes y socios, jefes de taller, vigilantes o celadores.

      div
        h3 
        p Todo dao material causado intencionalmente a los edificios, obras, maquinarias y materias primas, instrumentos y dems objetos relacionados con el trabajo, y toda grave negligencia que ponga en peligro la seguridad de las personas o de las cosas.

      div
        h3 
        p Todo acto inmoral o delictuoso que el trabajador cometa en el taller, establecimiento o lugar de trabajo o en el desempeo de sus labores.

      div
        h3 
        p Cualquier violacin grave de las obligaciones o prohibiciones especiales que incumben al trabajador de acuerdo con los Artculos 58 y 60 del CST, o cualquier falta grave calificada como tal en pactos o convenciones colectivas, fallos arbitrales, contratos individuales o reglamentos. 

      div
        h3 
        p La detencin preventiva del trabajador por ms de treinta (30) das, a menos que posteriormente sea absuelto; o el arresto correccional que exceda de ocho (8) das, o an por tiempo menor, cuando la causa de la sancin sea suficiente por s misma para justificar la extincin del contrato. 

      div
        h3 
        p El que el trabajador revele los secretos tcnicos o comerciales o d a conocer asuntos de carcter reservado, con perjuicio de la empresa.

      div
        h3 
        p El deficiente rendimiento en el trabajo en relacin con la capacidad del trabajador y con el rendimiento promedio en labores anlogas, cuando no se corrija en un plazo razonable a pesar del requerimiento del empleador.

      div
        h3 
        p La sistemtica inejecucin, sin razones vlidas, por parte del trabajador, de las obligaciones convencionales o legales.

      div
        h3 
        p Todo vicio del trabajador que perturbe la disciplina del establecimiento.

      div
        h3 
        p La renuencia sistemtica del trabajador a aceptar las medidas preventivas, profilcticas o curativas, prescritas por el mdico del empleador o por las autoridades para evitar enfermedades o accidentes.

      div
        h3 
        p La ineptitud del trabajador para realizar la labor encomendada.

      div
        h3 
        p El reconocimiento al trabajador de la pensin de la jubilacin o invalidez estando al servicio de la empresa.
      
      div
        h3 
        p La enfermedad contagiosa o crnica del trabajador, que no tenga carcter de profesional, as como cualquiera otra enfermedad o lesin que lo incapacite para el trabajo, cuya curacin no haya sido posible durante ciento ochenta (180) das. El despido por esta causa no podr efectuarse sino al vencimiento de dicho lapso y no exime al empleador de las prestaciones e indemnizaciones legales y convencionales derivadas de la enfermedad. (CST, 2020, art. 62 prr. 1-15)

    p.mb-5 El CST consagra las <b>causas justas</b> para dar por terminado unilateralmente el contrato de trabajo, por parte del <b>trabajador.</b>

